.article {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__preview {
    margin-right: 30px;
  }

  &__title {
    color: var(--color-black);
    
    margin-bottom: 20px;
  }

  &__text {
    font-size: 25px;
    line-height: 46px;
    color: var(--color-black);

    max-width: 757px;
  }
}

@media (max-width: 1200px) {
  .article {
    flex-direction: column;

    &__preview {
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__text {
      max-width: none;
    }
  }
}

@media (max-width: 768px) {
  .article {

    &__text {
      font-size: 20px;
      line-height: 165%;
    }
  }
}