.cart {
  padding-top: 40px;
  padding-bottom: 155px;

  &__row {
    display: flex;
    align-items: flex-start;
  }

  &__shop {
    margin-right: 50px;

    &-table {

      td {
        vertical-align: middle;
      }
    }
  }
}

.cart-mobile {
  display: none;
  padding-bottom: 30px;

  border-bottom: 1px solid #D4D4D4;

  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__img {
    margin-right: 15px;
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-family: 'Outfit', sans-serif;
    font-size: 18px;
    line-height: 23px;
    color: var(--color-monsoon);

    margin-bottom: 12px;
  }

  &__price {
    display: flex;
    align-items: center;

    &-val {
      font-weight: 600;
      font-size: 18px;
    }

    &-full {
      color: #FF5B5B;

      margin-right: 10px;
    }

    &-old {
      color: #9A9A9A;
    }

    &-invalid {
      margin-right: 5px;
    }
  }

  &__changes {
    display: flex;
    align-items: flex-end;
  }

  &__colors {
    display: flex;
    margin-right: 20px;

    &-item {
      width: 23px;
      height: 23px;
      margin-right: 6px;

      border-radius: 100%;

      &:last-child {
        margin-right: 0;
      }

      &--active {
        border: 1.5px solid #A7A7A7;
      }

      &--black {
        background-color: #25282A;
      }

      &--light {
        background-color: #F0E9DE;
      }

      &--gray {
        background-color: #AAADAE;
      }

      &--green {
        background-color: #016262;
      }

      &--orange {
        background-color: #F8796B;
      }
    }
  }

  &__remove {
    display: flex;
    align-items: center;

    &-text {
      color: #545454;

      margin-right: 2px;
    }
  }
}

@media(max-width: 1300px) {
  .cart {

    &__row {
      align-items: stretch;
      flex-direction: column;
    }

    &__shop {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }
}

@media(max-width: 768px) {
  .cart {
    padding-bottom: 50px;
  }

  .cart-mobile {
    display: block;
  }
}

@media(max-width: 420px) {
  .cart-mobile {

    &__info {
      margin-bottom: 10px;
    }

    &__title  {
      max-width: 180px;
    }

    &__price {
      flex-direction: column;
      align-items: flex-start;

      &-full {
        margin-right: 0;
        margin-bottom: 5px;
      }
    }

    &__changes {
      flex-direction: column;
      align-items: flex-start;
    }

    &__colors {
      margin-bottom: 10px;
    }
  }
}