.product {
  padding-top: 40px;
  padding-bottom: 150px;

  &__flex {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__imgs {
    margin-right: 30px;
  }

  &__cont {
    max-width: 659px;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__price {
    display: flex;
    align-items: center;

    margin-bottom: 20px;

    &-text {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
    }

    &-real {
      color: #ff5b5b;
      margin-right: 11px;
    }

    &-old {
      color: #c4c4c4;

      span {
      color: #ff5b5b;
      }
    }
  }

  &__rating {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    &-stars {
      margin-right: 7px;
    }

    &-text {
      font-size: 25px;
      color: #a9a9a9;
    }

    &-value {
      margin-right: 7px;
    }
  }

  &__colors {
    margin-bottom: 30px;

    &-caption {
      font-size: 25px;
      color: var(--color-black);

      margin-bottom: 11px;
    }

    &-list {
      display: flex;
    }

    &-item {
      width: 51px;
      height: 51px;
      margin-right: 15px;

      border-radius: 100%;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        cursor: pointer;
      }

      &--black {
        background-color: #25282a;
      }

      &--light {
        background-color: #f0e9de;
      }

      &--gray {
        background-color: #AAADAE;
      }

      &--darkgreen {
        background-color: #016262;
      }

      &--orange {
        background-color: #F8796B;
      }
    }
  }

  &__add {
    width: 100%;
    margin-bottom: 30px;
    padding: 35px 20px;

    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    letter-spacing: 0.265em;
    text-transform: uppercase;
    text-align: center;
    color: var(--color-white);

    background-color: var(--color-teak);
    transition: background-color .2s ease-in-out;

    &:hover {
      transition: background-color .2s ease-in-out;
      background-color: #ce9477;
    }
  }

  &__payment {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;

    &-guar {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
    }

    &-text {
      font-size: 25px;
      color: var(--colo-black);

      margin-right: 5px;
    }

    &-cards {
      display: flex;
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 71px;
      height: 71px;
      margin-right: 27px;

      border: 1px solid #D3D3D3;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__delivery {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    padding: 30px;

    background-color: #f3f3f3;

    &-icon {
      margin-right: 30px;
    }

    &-info {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      letter-spacing: 0.265em;
      text-transform: uppercase;
      color: #545454;

      margin-bottom: 10px;
    }

    &-text {
      font-size: 25px;
      line-height: 42px;
      color: #545454;
    }
  }
}

.product-tabs {

  &__list {
    display: flex;

    box-shadow: inset 0px -1px 0px #EEEEEE;
  }

  &__item {
    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    color: #9E9E9E;

    margin-right: 40px;
    padding-bottom: 13px;

    &:hover {
      cursor: pointer;
    }

    &:last-child {
      margin-right: 0;
    }

    &--active {
      font-weight: 400;
      color: var(--color-secondary);

      box-shadow: inset 0px -2px 0px #333333;
    }
  }

  &__cont {
    display: none;
    margin-top: 30px;

    &--active {
      display: block;
    }

    &-title {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      color: var(--color-secondary);

      margin-bottom: 20px;
    }

    &-text {
      font-size: 25px;
      line-height: 160%;
      color: var(--color-gray);
    }
  } 
}

.product__colors-item.selected {
  border: 4px solid #a7a7a7;
}

.product-swiper {
  width: 994px;

  &__wrapper {
    height: 1000px;
    margin-bottom: 30px;
  }

  &__slide {
    background-image: url('../img/product-swiper-img.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__controllers {
    display: flex;
    align-items: center;
  }

  &__nav {
    width: 125px;
    height: 125px;

    &--prev {
      margin-right: 20px;
    }

    &--next {
      margin-left: 20px;
    }
  }

  &__pagination {
    display: flex;

    &-item {
      max-width: 125px;
      max-height: 125px;
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }

      &--active {
        border: 4px solid rgb(194, 194, 10);
      }
    }
  }
}

@media(max-width: 1600px) {
  .product-swiper {
    width: 800px;

    &__wrapper {
      height: 700px;
    }
  }
}

@media(max-width: 1400px) {
  .product {

    &__flex {
      flex-direction: column;
      align-items: center;
    }

    &__imgs {
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__cont {
      max-width: 100%;
    }
  }

  .product-swiper {
    width: 1000px;

    &__controllers {
      justify-content: center;
    }
  }
}

@media(max-width: 998px) {
  .product-swiper {
    width: 800px;

    &__wrapper {
      height: 500px;
    }

    &__controllers {
      display: none;
    }
  }
}

@media(max-width: 768px) {
  .product {
    padding-bottom: 50px;

    &__price {
      
      &-text {
        font-size: 20px;
      }
    }

    &__rating {

      &-stars {
        max-width: 170px;
      }

      &-text {
        font-size: 18px;
      }
    }

    &__colors {

      &-item {
        width: 30px;
        height: 30px;
      }
    }

    &__add {
      font-size: 20px;

      padding: 20px;
    }

    &__delivery {
      padding: 15px;

      &-info {
        font-size: 20px;
      } 

      &-text {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }

  .product__colors-item.selected {
    border: 2px solid #a7a7a7;
  }

  .product-swiper {
    width: 600px;
    height: 400px;

    &__wrapper {
      height: 400px;
    }
  }

  .product-tabs {

    &__item {
      font-size: 20px;

      margin-right: 20px;
    }

    &__cont {
      margin-top: 15px;

      &-title {
        margin-bottom: 10px;
      }

      &-text {
        font-size: 18px;
      }
    }
  }
}

@media(max-width: 520px) {
  .product {

    &__imgs {
      margin-bottom: 15px;
    }

    &__title {
      margin-bottom: 10px;
    }

    &__rating {
      margin-bottom: 20px;

      &-stars {
        width: 120px;
      }
    }

    &__colors {

      &-caption {
        font-size: 20px;
      }
    }

    &__add {
      font-size: 18px;
      
      margin-bottom: 20px;
      padding: 15px;
    }

    &__payment {

      &-guar {
        margin-bottom: 10px;
      }

      &-text {
        font-size: 18px;
      }

      &-lock {
        width: 20px;
        height: 20px;
      }

      &-btn {
        width: 60px;
        height: 60px;
        margin-right: 15px;
      }
    }

    &__delivery {
      padding: 10px 5px;
      margin-bottom: 30px;

      &-icon {
        width: 50px;
        height: 50px;
        margin-right: 15px;
      }

      &-info {
        font-size: 16px;
      }

      &-text {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }

  .product-swiper {
    width: 500px;
  }

  .product-tabs {

    &__list {
      align-items: center;
      overflow-x: auto;
    }

    &__item {
      font-size: 18px;

      padding-bottom: 8px;
    }

    &__cont {

      &-title {
        font-size: 18px;
      }

      &-text {
        font-size: 16px;
      }
    }
  }
}