.about-hero {
  min-height: 750px;
  padding: 100px 0;

  background-color: var(--color-mine);
  background-image: url('../img/about-hero-bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__text {
    font-size: 25px;
    line-height: 40px;
    color: var(--color-white);
    text-align: center;

    max-width: 1014px;
    margin-bottom: 70px;
  }

  &__link {
    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    text-align: center;
    letter-spacing: 0.265em;
    text-transform: uppercase;
    color: var(--color-white);

    padding: 32px 52px;
    background-color: var(--color-teak);
    transition: background-color .2s ease-in-out;

    &:hover {
      transition: background-color .2s ease-in-out;
      background-color: var(--color-gray);
    }
  }
}

@media (max-width: 768px) {
  .about-hero {
    min-height: 480px;
    padding: 50px 0;

    &__title {
      margin-bottom: 25px;
      line-height: 1;
    }

    &__text {
      font-size: 20px;
      line-height: 167%;

      margin-bottom: 35px;
    }

    &__link {
      font-size: 18px;

      padding: 21px 30px;
    }
  }
}

@media (max-width: 375px) {
  .about-hero {

    &__link {
      padding: 20px;
    }
  }
}