.com {
  position: relative;

  background-image: url('../img/com-bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__wrapper {
    padding: 110px 0;

    background-color: rgba($color: #000000, $alpha: 0.6);
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__ico {
    margin-bottom: 50px;
  }

  &__caption {
    font-family: 'Outfit', sans-serif;
    font-weight: 500;
    font-size: 26px;
    line-height: 205%;
    letter-spacing: 7px;
    color: var(--color-fantazy);
    text-align: center;
    text-transform: uppercase;

    margin-bottom: 12px;
  }

  &__descr {
    font-family: 'Outfit', sans-serif;
    font-weight: 400;
    font-size: 75px;
    line-height: 112px;
    color: var(--color-fantazy);
    text-transform: capitalize;
    text-align: center;

    max-width: 1360px;

    &-span {
      text-transform: uppercase;
      font-weight: 600;

      &--ttc {
        text-transform: capitalize;
      }
    }
  }
}

@media(max-width: 1024px) {
  .com {

    &__caption {
      font-size: 20px;

      margin-bottom: 30px;
    }
    
    &__descr {
      font-size: 40px;
      line-height: 168%;
    }
  }
}

@media(max-width: 768px) {
  .com {

    &__wrapper {
      padding: 72px 0;
    }
  }
}

@media(max-width: 420px) {
  .com {

    &__descr {
      font-size: 30px;
      line-height: 168%;
    }
  }
}