.furniture {
  padding-bottom: 100px;

  &__flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__left {
    margin-right: 20px;
  }

  &__title {
    line-height: 156.84%;

    max-width: 921px;
    margin-bottom: 70px;
  }

  &__text {
    font-size: 25px;
    line-height: 43px;
    color: var(--color-black);

    max-width: 847px;
    margin-bottom: 82px;
  }

  &__author {
    display: flex;
    align-items: center;

    &-rating {
      margin-right: 30px;
    }

    &-name {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      letter-spacing: 0.265em;
      color: var(--color-black);
      text-transform: uppercase;
    }
  }

  &__swiper {
    width: 705px;
    margin-left: 0;
    margin-right: 0;

    &-wrapper {
      height: 836px;
      margin-bottom: 35px;
    }

    &-slide {
      background-image: url('../img/furniture-swiper.jpg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    &-navigation {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &-btn {
      width: 50px;
      height: 50px;
      margin-top: 0;

      background-size: 25px 22px;

      &--prev {
        margin-right: 5px;
        background-image: url('../img/furniture-btn-prev.svg');
      }

      &--next {
        margin-left: 5px;
        background-image: url('../img/furniture-btn-next.svg');
      }
    }
  }
}

@media(max-width: 976px) {
  .furniture {
    padding-bottom: 40px;

    &__flex {
      flex-direction: column;
      justify-content: center;
    }

    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__title {
      margin-bottom: 25px;
    }

    &__text {
      text-align: center;
      margin-bottom: 20px;
    }

    &__author {

      &-name {
        font-size: 20px;
      }
    }

    &__swiper {

      &-btn {
        display: block;
      }
    }
  }
}

@media(max-width: 768px) {
  .furniture {

    &__swiper {
      max-width: 450px;

      &-wrapper {
        height: 500px;
        margin-bottom: 0;
      }

      &-navigation {
        display: none;
      }
    }
  }
}

@media(max-width: 520px) {
  .furniture {

    &__title {
      line-height: 44px;
    }

    &__text {
      font-size: 18px;
      line-height: 175%;
    }

    &__author {
      flex-direction: column;

      &-rating {
        margin-right: 0;
        margin-bottom: 20px;
      }

      &-name {
        line-height: 25px;
      }
    }

    &__swiper {
      max-width: 380px;

      &-wrapper {
        height: 420px;
      }
    }
  }
}

@media(max-width: 420px) {
  .furniture {

    &__swiper {
      max-width: 350px;

      &-wrapper {
        
      }
    }
  }
}

@media(max-width: 375px) {
  .furniture {

    &__swiper {
      max-width: 280px;

      &-wrapper {
        height: 320px;
      }
    }
  }
}