.benefit {
  padding-bottom: 100px;

  &__list {
    display: flex;
    padding: 77px 125px 77px 130px;

    background-color: var(--color-thumb);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 435px;
    padding-right: 96px;
    margin-right: 87px;

    border-right: 1.8px solid rgba(255, 255, 255, 0.3);

    &:last-child {
      margin-right: 0;
      padding-right: 0;
      border-right: none;
    }

    &-img {
      margin-bottom: 27px;
    }

    &-caption {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      line-height: 32px;
      text-align: center;
      letter-spacing: 0.265em;
      text-transform: uppercase;
      color: var(--color-white);

      margin-bottom: 17px;
    }

    &-descr {
      font-size: 25px;
      line-height: 25px;
      color: var(--color-white);
      text-align: center;
    }
  }
}

@media(max-width: 1150px) {
  .benefit {

    &__list {
      padding: 77px 60px;
    }

    &__item {
      padding-right: 50px;
      margin-right: 41px;
    }
  }
}

@media(max-width: 976px) {
  .benefit {

    &__item {

      &-caption {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 15px;
      }

      &-descr {
        font-size: 18px;
        line-height: 29px;
      }
    }
  }
}

@media(max-width: 768px) {
  .benefit {
    padding-bottom: 40px;
    
    &__list {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 60px;
    }

    &__item {
      padding-right: 0;
      padding-bottom: 36px;
      margin-right: 0;
      margin-bottom: 36px;
      border: none;
      border-bottom: 1.8px solid rgba(255, 255, 255, 0.3);
      
      &:last-child {
        margin-bottom: 0;
        padding-bottom: 0;
        border: none;
      }
    }
  }
}

@media(max-width: 420px) {
  .benefit {

    &__list {
      padding: 30px;
    }
  }
}