.catalog-hero {
  padding-top: 40px;
  padding-bottom: 100px;

  &__cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 420px;
    padding: 40px 0;

    background-image: url('../img/catalog-hero-bg.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__title {
    margin-bottom: 40px;
    text-align: center;
  }

  &__warning {
    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    letter-spacing: 0.265em;
    text-transform: uppercase;
    color: var(--color-white);
  }
}

@media(max-width: 998px) {
  .catalog-hero {

    &__title {
      font-size: 45px;
    }
  }
}

@media(max-width: 768px) {
  .catalog-hero {
    padding: 40px 0;

    &__container {
      padding: 0;
    }

    &__cont {
      min-height: 200px;
    }

    &__title {
      margin-bottom: 12px;
      font-size: 35px;
      line-height: 35px;
    }
  }
}

@media (max-width: 520px) {
  .catalog-hero {

    &__title {
      font-size: 30px;
    }

    &__warning {
      font-size: 20px;
    }
  }
}