.unique {
  padding: 100px 0;

  &__title {
    text-align: center;
  }

  &__subtitle {
    text-align: center;
    margin-bottom: 40px;
  }

  &__grid {

    &-item {

      &--1 {
          background-image: url('../img/unique-1.png');
      }

      &--2 {
        background-image: url('../img/unique-2.png');
      }

      &--3 {
        background-image: url('../img/unique-3.png');
      }

      &--4 {
        background-image: url('../img/unique-4.png');
      }

      &--5 {
        background-image: url('../img/unique-5.png');
      }
    }
  }
}

@media(max-width: 768px) {
  .unique {
    padding: 40px 0;

    &__title {
      text-align: left;
    }

    &__subtitle {
      text-align: left;
      font-size: 16px;
    }
  }
} 