.product-descr {
  padding-bottom: 100px;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__preview {
    margin-right: 30px;
  }

  &__title {
    color: var(--color-black);
    margin-bottom: 30px;
  }

  &__text {
    font-size: 25px;
    line-height: 160%;
    color: #848484;

    max-width: 740px;
  }
}

@media (max-width: 1400px) {
  .product-descr {

    &__preview {
      max-width: 600px;
    }
  }
}

@media (max-width: 1200px) {
  .product-descr {

    &__container {
      flex-direction: column;
      align-items: flex-start;
    }

    &__preview {
      max-width: none;
      width: 100%;
      height: 600px;
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__text {
      max-width: none;
    }
  }
}

@media (max-width: 768px) {
  .product-descr {
    padding-bottom: 40px;

    &__preview {
      height: 400px;

      margin-bottom: 20px;
    }

    &__title {
      margin-bottom: 15px;
      line-height: 35px;
    }

    &__text {
      font-size: 18px;
    }
  }
}

@media (max-width: 520px) {
  .product-descr {

    &__preview {
      height: 300px;
    }

    &__text {
      font-size: 16px;
    }
  }
}