.cart-table {
  max-width: 1197px;

  border: 1px solid #b6b6b6;

  &__row {
    border-bottom: 1px solid #B6B6B6;
  }

  &__caption {
    text-align: left;
    font-weight: 400;
    font-size: 25px;
    color: var(--color-secondary);

    padding: 12px 0;

    &--products {
      padding-left: 78px;
    }

    &--quantity {
      padding-right: 129px;
    }

    &--total {
      padding-right: 250px;
    }
  }

  &__product {
    border-bottom: 1px solid #B6B6B6;

    &-info {
      display: flex;
      align-items: center;
      padding: 15px 95px 20px 71px;
    }

    &-img {
      margin-right: 35px;
    }

    &-title {
      font-size: 25px;
      line-height: 33px;
      color: var(--color-monsoon);

      max-width: 240px;
    }

    &-del {
      padding-right: 70px;
    }
  }

  &__quantity {
    display: flex;

    &-actions {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 38px;
      height: 40px;

      border: 1px solid #E4E4E4;
    }

    &-minus {

      span {
        display: block;
        width: 10px;
        height: 1px;
        background-color: var(--color-secondary);
      }
    }

    &-current {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 38px;
      height: 40px;

      color: var(--color-secondary);
    
      border: 1px solid #E4E4E4;
    }
  }

  &__total {
    font-size: 25px;
    color: var(--color-secondary);
  }

  &__del {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 27px;

    &-icon {
      display: block;
      width: 14px;
      height: 2px;

      position: relative;

      transform: rotate(45deg);
      background-color: var(--color-secondary);

      &::before {
        content: "";

        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        transform: rotate(90deg);
        background-color: var(--color-secondary);
      }
    }
  }
}

.cart-table__del:hover .cart-table__del-icon {
  background-color: red;
}

.cart-table__del:hover .cart-table__del-icon::before {
  background-color: red;
}

@media(max-width: 1600px) {
  .cart-table {

    &__caption {

      &--quantity {
        padding-right: 60px;
      }

      &--total {
        padding-right: 100px;
      }
    }

    &__product {

      &-info {
        padding-right: 50px;
      }

      &-del {
        padding-right: 30px;
      }
    }
  }
}

@media(max-width: 1300px) {
  .cart-table {
    max-width: none;
    width: 100%;
  }
}

@media(max-width: 998px) {
  .cart-table {

    &__caption {
      font-size: 20px;

      &--products {
        padding-left: 20px;

        max-width: 330px;
      }
    }

    &__product {

      &-info {
        padding-left: 20px;
        padding-right: 15px;
      }

      &-img {
        margin-right: 15px;
      }

      &-title {
        font-size: 20px;
        line-height: 25px;

        max-width: 180px;
      }
    }
  }
}

@media(max-width: 768px) {
  .cart-table {
    display: none;
  }
}