.order {
  max-width: 470px;
  padding: 20px 20px 35px 20px;

  border: 1px solid #b6b6b6;

  &__total {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-caption {
      font-size: 20px;
      letter-spacing: 0.02em;
      color: var(--color-secondary);
    }

    &-price {
      font-size: 24px;
      letter-spacing: 0.02em;
      color: var(--color-secondary);
    }
  }

  &__subtotal {
    margin-bottom: 18px;
  }

  &__additionally {
    font-size: 20px;
    letter-spacing: 0.02em;
    color: #a9a9a9;
    text-align: right;

    margin-bottom: 40px;
  }

  &__options {
    margin-bottom: 40px;

    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 16px;
      padding: 12px 20px;

      background-color: #f9f9f9;
      border: 1px solid #d1d1d1;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-left {
      display: flex;
      align-items: center;
    }

    &-input {
      width: 0;
      height: 0;
      opacity: 0;
    }

    &-field {
      width: 24px;
      height: 24px;
      margin-right: 10px;

      border: 1.5px solid #d1d1d1;
      border-radius: 100%;
    }

    &-title {
      font-size: 18px;
      color: #151515;
    }

    &-price {
      font-size: 18px;
      color: #151515;
    }
  }

  &__finalprice {
    margin-bottom: 23px;
  }

  &__proceed {
    font-size: 20px;
    letter-spacing: 0.02em;
    color: var(--color-white);
    text-align: center;

    width: 100%;
    margin-bottom: 40px;
    padding: 24px;

    background-color: var(--color-teak);
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: #d09679;
    }
  }

  &__payment {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;

    &-item {
      margin-right: 27px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 71px;
      height: 71px;

      border: 0.780645px solid #d3d3d3;
    }
  }

  &__text {
    font-size: 20px;
    line-height: 125%; 
    text-align: center;
    letter-spacing: 0.02em;
    color: #808080;
  }
}

.order__options-input:checked + label {
  background-color: #dab76d;
}

@media(max-width: 1300px) {
  .order {
    max-width: 100%;
  }
}

@media(max-width: 520px) {
  .order {
    padding: 15px 10px 20px 10px;

    &__total {

      &-caption {
        font-size: 18px;
      }

      &-price {
        font-size: 20px;
      }
    }

    &__additionally {
      font-size: 16px;
    }

    &__options {

      &-item {
        padding: 12px 10px;
      }

      &-field {
        width: 18px;
        height: 18px;
      }

      &-title {
        font-size: 16px;
      }

      &-price {
        font-size: 16px;
      }
    }

    &__proceed {
      font-size: 18px;

      padding: 15px;
    }

    &__text {
      font-size: 16px;
    }
  }
}

@media(max-width: 420px) {
  .order {
    &__payment {
      display: grid;
      grid-template-columns: repeat(2, 71px);
      gap: 30px;
    }
  }
}

@media(max-width: 375px) {
  .order {

    &__options {

      &-item {
        flex-direction: column;
      }

      &-left {
        margin-bottom: 10px;
      }
    }
  }
}