.footer {
  padding-top: 56px;
  padding-bottom: 48px;

  background-color: var(--color-mine2);

  &__logo {
    margin-bottom: 53px;
    padding-bottom: 36px;
    border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
  }

  &__middle {
    display: flex;
    justify-content: space-between;
    margin-bottom: 66px;

    &-right {
      display: flex;
    }
  }

  &__office {
    margin-right: 20px;
    
    &-title {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      color: var(--color-white);
      letter-spacing: -0.309091px;

      margin-bottom: 24px;
    }

    &-address {
      font-size: 25px;
      line-height: 42px;
      color: rgba(255, 255, 255, 0.58);

      max-width: 329px;
      margin-bottom: 38px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;

    &-link {
      display: flex;
      align-items: center;
      margin-bottom: 27px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-ico {
      margin-right: 20px;
    }

    &-date {
      font-size: 25px;
      color: rgba(255, 255, 255, 0.58);

      &:hover {
      color: rgba(255, 255, 255, 0.8);
      }
    }
  }

  &__nav {
    margin-right: 82px;

    &:last-child {
      margin-right: 96px;
    }

    &-item {
      margin-bottom: 20px;

      &:first-child {
        margin-bottom: 24px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-title {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      letter-spacing: -0.309091px;
    
      color: var(--color-white);
    }

    &-link {
      font-weight: 500;
      font-size: 25px;
      line-height: 33px;
      color: rgba(255, 255, 255, 0.58);

      &:hover {
        color: rgba(255, 255, 255, 0.8);
      }
    }
  }

  &__join {

    &-caption {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      color: var(--color-white);
      letter-spacing: -0.4px;

      margin-bottom: 66px;
    }

    &-newsletter {
      font-size: 25px;
      line-height: 35px;
      color: var(--color-white);

      max-width: 450px;
    }
  }

  &__form {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    &-input {
      width: 353px;
      margin-right: 27px;
      padding: 13px 20px 20px 0;

      font-size: 20px;
      color: var(--color-white);
      border: none;
      border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
      background-color: transparent;

      &::placeholder {
        font-size: 25px;
        color: rgba($color: #fff, $alpha: 0.5);
        letter-spacing: -0.272727px;
      }
    }

    &-submit {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 66px;
      height: 66px;

      background-color: var(--color-white);
      border-radius: 10.6667px 10.6667px 0px 0px;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  &__policy {
    display: flex;
    align-items: center;
    margin-right: 30px;

    &-item {
      margin-right: 22px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-link {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      color: var(--color-white);
      letter-spacing: -0.272727px;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__follow {

    &-title {
      display: none;
      font-family: 'Outfit', sans-serif;
      font-weight: 400;
      font-size: 26px;
      color: var(--color-white);

      margin-bottom: 37px;
    }
  }

  &__social {
    display: flex;
    align-items: center;

    &-link {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__payment {
    display: flex;

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 71px;
      height: 71px;
      margin-right: 27px;
      
      background-color: var(--color-white);
      border: 0.780645px solid #d3d3d3;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}


@media(max-width: 1400px) {
  .footer {

    &__middle {
      flex-wrap: wrap;
    }
  }
}

@media(max-width: 1355px) {
  .footer {

    &__office {
      width: 100%;
      margin-bottom: 30px;
      padding-bottom: 33px;

      border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
    }

    &__middle {

      &-right {
        width: 100%;
        justify-content: space-between;
        padding-bottom: 45px;
        border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
      }
    } 
  }
}

@media(max-width: 976px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 54px;

    &__logo {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__middle {
      margin-bottom: 33px;

      &-right {
        flex-direction: column;
      }
    }

    &__nav {
      margin-right: 0;
      margin-bottom: 30px;
      padding-bottom: 33px;

      border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
    }
  }
}

@media(max-width: 768px) {
  .footer {
    
    &__office {

      &-title {
        margin-bottom: 28px;
      }

      &-address {
        margin-bottom: 8px;
      }
    }
    
    &__contacts {

      &-date {
        font-size: 23px;
      }

      &-link {
        margin-bottom: 22px;
      }
    }

    &__join {

      &-caption {
        line-height: 42px;

        margin-bottom: 22px;
      }
    }

    &__form {
      margin-bottom: 30px;

      &-input {

        &::placeholder {
          font-size: 20px;
        }
      }
    }

    &__row {
      flex-direction: column-reverse;
      align-items: flex-start;
    }

    &__policy {
      margin-right: 0;

      &-item {
        margin-right: 14px;
      }

      &-link {
        font-size: 18px;
      }
    }

    &__follow {
      margin-bottom: 36px;

      &-title {
        display: block;
      }
    }
  }
}

@media(max-width: 520px) {
  .footer {

    &__join {

      &-newsletter {
        font-size: 16px;
      }
    }

    &__form {

      &-input {
        margin-right: 0;
      }

      &-submit {
        width: 50px;
        height: 50px;
      }
    }

    &__policy {
      flex-direction: column;
      align-items: flex-start;

      &-item {
        margin-right: 0;
        margin-bottom: 14px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &-link {
        font-size: 20px;
      }
    }
  }
}

@media(max-width: 375px) {
  .footer {

    &__form {

      &-input {
        max-width: 250px;
      }

      &-submit {
        width: 40px;
        height: 40px;  
      }
    }

    &__payment {
      display: grid;
      grid-template-columns: repeat(2, 71px);
      gap: 30px;

      &-btn {
        margin-right: 0;
      }
    }
  }
}