html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'HK Grotesk', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--color-black);
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  color: inherit;
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

input [ type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1749px;
}

.main-title {
  font-family: 'Outfit', sans-serif;
  font-weight: 500;
  font-size: 75px;
  line-height: 94px;
  color: var(--color-white);

  margin-bottom: 18px;

  &--dark {
    color: var(--color-cod-gray);
  }
}

.section-title {
  font-family: 'Outfit', sans-serif;
  font-weight: 400;
  font-size: 65px;
  line-height: 82px;
  color: var(--color-white);
}

.heading-3 {
  font-family: 'Outfit', sans-serif;
  font-weight: 300;
  font-size: 55px;
  line-height: 69px;
  color: var(--color-black);

  margin-bottom: 30px;
}

.page-nav {
  display: flex;
  align-items: center;
  margin-bottom: 45px;

  &__link {  
    font-size: 18px;
    letter-spacing: 0.015em;
    text-transform: uppercase;
    color: var(--color-mine);

    &:hover {
      text-decoration: underline;
    }

    &--prev {
      color: var(--color-slate);
      margin-right: 8px;
    }
  }

  &__separator {
    display: block;
    margin-right: 8px;

    font-size: 17px;
    letter-spacing: 0.015em;
    color: #bdbdbd;    
  }
}

@media(max-width: 976px) {
  .main-title {
    font-size: 40px;
    line-height: 130%;
    text-align: center;

    margin-bottom: 16px;
  }
}

.main-subtitle {
  font-size: 25px;
  line-height: 33px;
  color: var(--color-cod-gray);
}

@media(max-width: 768px) {
  .main-subtitle {
    font-size: 16px;
    line-height: 29px;
  }
}

.swiper {
  max-width: 1534px;
}

.swiper .swiper-slide {
  box-sizing: border-box;
}

.swiper-button-next,
.swiper-button-prev {
  position: static;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 40px;

  &::after {
    display: none;
  }
}

.swiper-button-next {
  background-image: url('../img/swiper-button-next.svg');
}

.swiper-button-prev {
  background-image: url('../img/swiper-button-prev.svg');
}

@media(max-width: 1400px) {
  .swiper-button-next,
  .swiper-button-prev {
    background-size: 15px 35px;
  }
}

@media(max-width: 1100px) {
  .swiper-button-next,
  .swiper-button-prev {
    background-size: 10px 25px;
  }
}

@media(max-width: 976px) {
  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }
}

@media (max-width: 768px) {
  .section-title {
    font-size: 35px;
  }

  .main-title {
    font-size: 35px;
    line-height: 120%;
  }

  .heading-3 {
    font-size: 35px;
    line-height: 35px;
    margin-bottom: 25px;
  }
}

@media (max-width: 520px) {
  .heading-3 {
    font-size: 28px;
  }

  .page-nav {
    margin-bottom: 25px;

    &__link {
      font-size: 16px;
    }
  }
}

.swiper-pagination {
  position: static;
}

.swiper-pagination-fraction {
  width: 81px;
}

.swiper-pagination-current, 
.swiper-pagination-total {
  font-weight: 400;
  font-size: 30px;
  color: var(--color-black);
}