.hero {
  padding: 214px 0;

  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../img/hero-bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--color-mine);

  &__subtitle {
    font-size: 25px;
    line-height: 42px;
    color: var(--color-white);

    max-width: 825px;
    margin-bottom: 55px;
  }

  &__btn {
    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    letter-spacing: 0.265em;
    text-transform: uppercase;
    color: var(--color-white);

    padding: 32px 52px;
    background-color: var(--color-teak);
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: var(--color-monsoon);
    }
  }
}

// media

@media(max-width: 998px) {
  .hero {
    padding: 100px 0;

    &__title {
      font-size: 55px;
      line-height: 70px;
    }
  }
}

@media(max-width: 768px) {
  .hero {
    padding: 75px 0;

    &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    &__title {
      font-size: 35px;
      line-height: 50px;
      text-align: center;
      
      margin-bottom: 40px;
    }

    &__subtitle {
      display: none;
    }

    &__btn {
      font-size: 18px;
      padding: 21px 34px;
    }
  }
}

@media(max-width: 420px) {
  .hero {

    &__title {
      font-size: 35px;

      max-width: 351px;
      margin-bottom: 20px;
    }

    &__btn {
      font-size: 18px;

      padding: 20px 15px;
    }
  }
}