.contact {
  padding-top: 70px;
  padding-bottom: 100px;

  &__title {
    color: var(--color-black);
    text-align: center;

    margin-bottom: 30px;
  }

  &__subtitle {
    font-size: 25px;
    line-height: 172%;
    text-align: center;

    margin-bottom: 104px;  
  }
  
  &__wrapper {
    display: flex;
    justify-content: center;
  }

  &__list {
    display: flex;
    margin-bottom: 100px;

    &-item {
      display: flex;
      align-items: center;
      margin-right: 80px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width: 106px;
      height: 106px;
      margin-right: 26px;

      background-color: #a2beaa;
      border-radius: 100%;
    }

    &-svg {
      width: 40px;
      height: 40px;
      fill: var(--color-white);
    }

    &-caption {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      line-height: 32px;
      letter-spacing: 0.265em;
      text-transform: uppercase;
      color: #77808b;

      margin-bottom: 21px;
    }

    &-link {
      font-size: 25px;
      line-height: 33px;
      color: #283646;
    }
  }

  &__message {
    
    &-title {
      text-align: center;
      color: var(--color-black);

      margin-bottom: 30px;
    }

    &-subtitle {
      font-size: 25px;
      line-height: 43px;
      text-align: center;

      margin-bottom: 40px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;

    &-row {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 44px;
    }

    &-field {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-right: 150px;

      &:first-child {
        max-width: 710px;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    &-label {
      font-size: 25px;
      color: #212121;
      
      margin-bottom: 11px;
    }

    &-input {
      padding: 0;
      padding: 23px 26px;

      font-size: 20px;

      background-color: transparent;
      border: 1.4px solid #B5B5B5;
      border-radius: 11px;

      &::placeholder {
        font-size: 25px;
        color: #a2a9b0;
      }
    }
    
    &-message {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-bottom: 50px;
    }

    &-textarea {
      height: 257px;
      padding: 0;
      padding: 23px 25px;

      font-size: 20px;

      resize: none;
      background-color: transparent;
      border: 1.4px solid #B5B5B5;
      border-radius: 11px;

      &::placeholder {
        font-size: 25px;
        color: #a2a9b0;
      }
    }

    &-submit {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      text-align: center;
      letter-spacing: 0.265em;
      text-transform: uppercase;
      color: var(--color-white);

      padding: 32px 52px;
      background-color: var(--color-teak);
      transition: background-color .2s ease-in-out;

      &:hover {
        transition: background-color .2s ease-in-out;
        background-color: #ce9477;
      }
    }
  }
}

@media (max-width: 1200px) {
  .contact {

    &__list {

      &-item {
        margin-right: 20px;
      }

      &-icon {
        width: 80px;
        height: 80px;
      }
    }
  }
}

@media (max-width: 998px) {
  .contact {

    &__title {
      margin-bottom: 20px;
    }

    &__subtitle {
      margin-bottom: 30px;
    }

    &__list {
      flex-direction: column;
      margin-bottom: 70px;

      &-item {
        margin-right: 0;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &-caption {
        line-height: 25px;
      }
    }
  }
}

@media (max-width: 768px) {
  .contact {
    padding: 40px 0;

    &__subtitle {
      font-size: 20px;
      line-height: 168%;
    }

    &__message {

      &-title {
        margin-bottom: 15px;
      }

      &-subtitle {
        font-size: 20px;
        line-height: 160%;

        margin-bottom: 30px;
      }
    }

    &__form {
      
      &-row {
        flex-direction: column;
      }

      &-field {
        margin-right: 0;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }

        &:first-child {
          max-width: none;
        }
      }

      &-submit {
        font-size: 20px;

        padding: 27px 30px;
      }
    }
  }
}

@media (max-width: 520px) {
  .contact {

    &__list {

      &-caption {
        font-size: 20px;
        margin-bottom: 10px;
      }

      &-link {
        font-size: 20px;
      }
    }

    &__form {

      &-row {
        margin-bottom: 20px;
      }

      &-label {
        font-size: 20px;
      }

      &-input {
        padding: 15px 20px;

        &::placeholder {
          font-size: 17px;
        }
      }

      &-textarea {
        height: 165px;
        padding: 15px 17px;

        &::placeholder {
          font-size: 17px;
        }
      }
    }
  }
}