.catalog {
  padding-bottom: 100px;

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__filters {
    width: 355px;
    margin-right: 80px;
  }

  &__title {
    margin-bottom: 130px;
  }

  &__products {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-width: 100%;
      margin-bottom: 120px;
    }

    &-list {
      margin-bottom: 70px;
    }
  }

  &__showing {

    &-caption {
      margin-right: 20px;
    }

    &-text {
      font-size: 25px;
      color: var(--color-black);
    }
  }

  &__sort {
    display: flex;
    align-items: center;

    &-text {
      font-size: 25px;
      color: #1d1f1f;

      margin-right: 38px;
    }

    &-media {
      display: none;
    }
  }

  &__pagination {
    display: flex;

    &-item {
      margin-right: 50px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-link {
      font-family: 'Outfit', sans-serif;
      font-weight: 300;
      font-size: 25px;
      color: var(--color-black);
      text-transform: uppercase;

      display: flex;
      align-items: center;
      justify-content: center;
      width: 80px;
      height: 80px;
    
      border: 1.2px solid #545454;
      border-radius: 7.5px;

      &--active {
        background-color: var(--color-teak);
        border: 1px solid var(--color-teak);
        color: var(--color-white);
      }

      &--prev {
        background-image: url('../img/catalog-pagination-prev.svg');
        background-repeat: no-repeat;
        background-position: center;
      }

      &--next {
        background-image: url('../img/catalog-pagination-next.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
}

.catalog-droplist {
  width: 325px;
  padding: 21px 25px;

  background-color: var(--color-white);
  border: 1.5px solid #e3e3e3;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__text {
    font-size: 25px;
    color: #1d1f1f;
  }
}

.catalog-products {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(394px, 1fr));
  row-gap: 40px;
  column-gap: 51px;
  width: 100%;

  &__img {
    width: 100%;
    margin-bottom: 17px;
  }

  &__title {
    display: inline-block;
    font-size: 25px;
    line-height: 33px;
    color: var(--color-monsoon);

    margin-bottom: 15px;

    &:hover {
      text-decoration: underline;
    }
  }

  &__info {
    display: flex;
  }

  &__current {
    font-size: 25px;
    color: #ff5b5b;
  }

  &__realprice {
    margin-right: 10px;
  }

  &__discprice {
    font-size: 25px;
    color: #c4c4c4;
  }
}

.catalog-media {
  display: none;
  justify-content: space-between;

  &__filter {
    display: flex;
    align-items: center;

    &-icon {
      width: 30px;
      height: 30px;

      margin-right: 10px;
    }

    &-text {
      font-size: 25px;
      letter-spacing: 0.03em;
      color: var(--color-secondary);
    }
  }

  &__sort {

    &-wrapper {
      display: flex;
      align-items: center;
    }
  }
}

.catalog-filter {
  margin-bottom: 30px;
  padding-bottom: 30px;
  border-bottom: 2px solid #A3A3A3;

  &:last-child {
    margin-bottom: 38;
  }

  &__item {
    font-size: 25px;
    line-height: 33px;
    color: var(--color-monsoon);

    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    &--title {
      color: var(--color-mine);
    }

    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;

    &-icon {
      display: block;
      width: 20px;
      height: 2px;

      position: relative;

      background-color: var(--color-monsoon);

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;

        display: block;
        width: 100%;
        height: 2px;

        background-color: var(--color-monsoon);
        transform: rotate(90deg);
      }
    }
  }

  &__inner {
    display: none;
    margin-top: 30px;

    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 15px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-input {
      width: 0;
      height: 0;
      opacity: 0;
    }

    &-checkbox {
      width: 28px;
      height: 28px;
      margin-right: 30px;

      position: relative;

      border: 1px solid #DBE2EA;
      box-shadow: 0px 4px 8px rgba(44, 39, 56, 0.04);
      border-radius: 4px;

      &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) scale(0);
        transition: transform 0.1s ease-in;

        display: inline-block;
        width: 20px;
        height: 20px;

        background-image: url('../img/catalog-filter-checkbox.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }

    &-text {
      font-size: 25px;
      line-height: 33px;
      color: var(--color-monsoon);

      span {
        color: #aeaeae;
      }
    }
  }
}

.catalog-filter__inner-input:checked + label {
  border: 1px solid #000000;
  box-shadow: 0px 4px 8px rgba(44, 39, 56, 0.04);
  border-radius: 4px;
}

.catalog-filter__inner-input:checked + label::after {
  transform: translate(-50%, -50%) scale(1);
  transition: transform 0.1s ease-in;
}

.catalog__filter-accord.open .catalog-filter__inner {
  display: block;
}

.price-slider {

  &__range {
    width: 251px;
    margin-bottom: 20px;
  }

  &__inputs {
    display: flex;
  }

  &__field {
    display: flex;
    flex-direction: column;
    margin-right: 33px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__label {
    font-size: 25px;
    color: var(--color-monsoon);

    margin-bottom: 4px;
  }

  &__input {
    width: 109px;
    height: 42px;
    padding: 5px 10px;

    font-size: 20px;

    background-color: #f9f9f9;
    border: 1px solid #d1d1d1;

    &::placeholder {
      font-size: 25px;
      color: var(--color-monsoon);
    }
  }
}

.noUi-target {
  background-color: #ebebeb;
  border-radius: 12px;
  box-shadow: none;
  border: none;
}

.noUi-horizontal {
  height: 6px;
}

.noUi-connect {
  background-color: var(--color-black);
}

.noUi-handle::after,
.noUi-handle::before {
  display: none;
}

.noUi-horizontal .noUi-handle {
  width: 20px;
  height: 20px;
}

.noUi-handle {
  background-color: var(--color-white);
  border: 1px solid #d1d1d1;
  border-radius: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
}

@media (max-width: 1200px) {
  .catalog {

    &__title {
      line-height: 45px;

      margin-bottom: 40px;
    }

    &__products {

      &-top {
        margin-bottom: 50px;
      }
    }

    &__sort {

      &-text {
        margin-right: 10px;
      }

      &-media {
        display: block;
      }
    }
  }

  .catalog-products {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }

  .catalog-droplist {
    display: none;
  }
}

@media (max-width: 1024px) {
  .catalog {

    &__container {
      flex-direction: column;
    }

    &__showing {
      align-self: end;
    }

    &__sort {
      display: none;
    }

    &__products {

      &-top {
        flex-direction: column;
        align-items: stretch;
      }
    }
  }

  .catalog-filter {
    display: none;
  }

  .catalog-media {
    display: flex;
    margin-bottom: 20px;
  }

  .catalog-products {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .catalog {
    padding-bottom: 40px;

    &__title {
      margin-bottom: 20px;
    }

    &__products {

      &-top {
        margin-bottom: 30px;
      }
    }

    &__showing {

      &-text {
        font-size: 20px;
      }
    }

    &__sort {

      &-text {
        font-size: 20px;
      }
    }

    &__pagination {

      &-item {
        margin-right: 30px;
      }

      &-link {
        font-size: 20px;

        width: 60px;
        height: 60px;
      }
    }
  }

  .catalog-media {

    &__filter {

      &-icon {
        width: 24px;
        height: 24px;
      }

      &-text {
        font-size: 20px;
      }
    }
  }

  .catalog-products {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));

    &__img {
      height: 300px;
    }

    &__title {
      font-size: 20px;

      margin-bottom: 8px;
    }

    &__current {
      font-size: 20px;
    }

    &__discprice {
      font-size: 20px;
    }
  }
}

@media (max-width: 520px) {
  .catalog {

    &__showing {

      &-text {
        font-size: 16px;
      }

      &-caption {
        margin-right: 9px;
      }
    }

    &__sort {

      &-text {
        font-size: 16px;
      }
    }

    &__pagination {

      &-item {
        margin-right: 15px;
      }

      &-link {
        font-size: 16px;

        width: 40px;
        height: 40px;
      }
    }
  }

  .catalog-media {

    &__filter {

      &-text {
        font-size: 16px;
      }
    }
  }
}