.accordion {
  width: 100%;
  max-width: 1430px;

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 41px;
    padding: 30px;

    border: 2px solid #dedede;
    border-radius: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      cursor: pointer;
    }
  }

  &__content {
    max-width: 1280px;
    margin-right: 40px;

    &-inner {
      display: none;
      margin-top: 28px;
    }
  }

  &__title {
    font-size: 25.384px;
    line-height: 33px;
    color: #130502;   
  }

  &__text {
    font-size: 25px;
    line-height: 160%;
    color: #827674;
  }

  &__control {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 41px;
    height: 41px;

    &-icon {
      display: block;
      width: 21px;
      height: 2px;

      position: relative;

      background-color: #130502;

      &::after {
        content: "";

        position: absolute;
        left: 0;
        top: 0;
        display: block;
        width: 100%;
        height: 100%;

        background-color: #130502;
        transform: rotate(90deg);
      }
    }
  }
}

.accordion__item.open {
  border: 2px solid var(--color-teak);
}

.accordion__item.open .accordion__title {
  color: var(--color-teak);
}

.accordion__item.open .accordion__control-icon::after {
  display: none;
}

.accordion__item.open .accordion__content-inner {
  display: block;
}

@media (max-width: 768px) {
  .accordion {

    &__item {
      margin-bottom: 25px;
      padding: 20px;
    }

    &__title {
      font-size: 20px;
      line-height: 155%;
    }

    &__content {

      &-inner {
        margin-top: 20px;
      }
    }

    &__text {
      font-size: 16px;
      line-height: 143%;
    }

    &__control {
      width: 25px;
      height: 25px;

      &-icon {
        width: 15px;
      }
    }
  }
}

@media (max-width: 520px) {
  .accordion {

    &__item {
      align-items: flex-start;
      padding: 10px;
    }

    &__content {
      margin-right: 25px;
    }
  }
}