.faq {
  padding-top: 85px;
  padding-bottom: 100px;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    color: #130502;

    margin-bottom: 50px;
  }
}

@media (max-width: 768px) {
  .faq {
    padding: 40px 0;

    &__title {
      margin-bottom: 20px;
    }
  }
}