.help {
  padding-top: 131px;
  padding-bottom: 112px;

  background-color: var(--color-gray);
  background-image: url('../img/help-bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__intro {
    margin-right: 30px;
  }

  &__title {
    margin-bottom: 10px;
  }

  &__subtitle {
    line-height: 32px;
    color: #c1c1c1;

    max-width: 714px;
    margin-bottom: 35px;
  }

  &__email {
    font-size: 25px;
    color: var(--color-white);

    &:hover {
      text-decoration: underline;
    }
  }

  &__info {

    &-worktime {
      font-size: 25px;
      color: var(--color-white);
    }
  }

  &__phone {
    display: flex;
    align-items: center;
    margin-bottom: 28px;
    padding: 20px 40px 20px 30px;

    font-size: 25px;
    color: var(--color-mine2);
  
    background-color: var(--color-white);

    &-ico {
      width: 40px;
      height: 40px;
      margin-right: 11px;
    }
  }
}

@media(max-width: 1100px) {
  .help {

    &__phone {
      padding: 20px 30px;
    }
  }
}

@media(max-width: 976px) {
  .help {
    padding: 96px 0;

    &__container {
      flex-direction: column;
    }

    &__intro {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__subtitle {
      text-align: center;
      margin-bottom: 20px;
    }
  }
}

@media(max-width: 768px) {
  .help {

    &__intro {
      margin-bottom: 10px;
    }

    &__subtitle {
      line-height: 23px;
      margin-bottom: 0;
    }

    &__email {
      display: none;
    }

    &__info {

      &-worktime {
        font-size: 22px;
      }
    }

    &__phone {

    }
  }
}