.ready {
  padding-bottom: 100px;

  &__title {
    text-align: center;
  }

  &__subtitle {
    text-align: center;
    margin-bottom: 27px;
  }

  &__product {
    display: flex;
    align-items: center;
  }

  &__swiper {

    &-top {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      max-width: 100%;
      height: 361px;
      margin-bottom: 23px;
    }

    &-title {
      font-family: 'Outfit', sans-serif;
      font-weight: 400;
      font-size: 25px;
      color: var(--color-black);

      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      margin-bottom: 10px;
    }

    &-price {
      font-size: 25px;
      color: var(--color-monsoon);

      margin-bottom: 14px;
    }

    &-view {
      font-size: 25px;
      color: var(--color-white);
      text-align: center;

      display: inline-block;
      width: 100%;
      padding: 20px;
      background-color: var(--color-teak);
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-gray);
      }
    }
  }
}
 
@media(max-width: 768px) {
  .ready {
    padding-bottom: 40px;

    &__title {
      text-align: left;
      margin-bottom: 16px;
    }

    &__subtitle {
      font-size: 16px;
      line-height: 29px;
      text-align: left;
    }

    &__swiper {

      &-title {
        font-size: 18px;

        margin-bottom: 7px;
      }

      &-price {
        font-size: 14px;
      }

      &-view {
        font-size: 14px;

        padding: 10px;
      }
    }
  }
}