.product-faq {
  padding-bottom: 100px;

  &__title {
    display: inline-block;
    margin-bottom: 30px;

    &-text {
      margin-bottom: 0;
    }
  }

  &__item {
    padding: 30px 35px 30px 0;
    border-top: 1px solid var(--color-slate);

    &:last-child {
      border-bottom: 1px solid var(--color-slate);
    }

    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-title {
      font-weight: 400;
      font-size: 25px;
      color: var(--color-mine);
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px;
    }

    &-icon {
      display: block;
      width: 34px;
      height: 4px;
  
      position: relative;
  
      background-color: var(--color-monsoon);
  
      &::after {
        content: "";
  
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        width: 100%;
        height: 100%;
  
        background-color: var(--color-monsoon);
        transform: rotate(90deg);
      }
    }
  }

  &__content {
    display: none;
    margin-top: 40px;

    &-text {
      font-size: 25px;
      line-height: 164%;
      color: var(--color-monsoon);

      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.product-faq__item.open .product-faq__content {
  display: block;
}

.product-faq__item.open .product-faq__item-icon::after {
  display: none;
}

@media (max-width: 768px) {
  .product-faq {
    padding-bottom: 40px;

    &__item {
      padding: 15px 15px 15px 0;

      &-title {
        font-size: 20px;
      }

      &-btn {
        width: 30px;
        height: 30px;
      }

      &-icon {
        width: 20px;
        height: 2px;
      }
    }

    &__content {
      margin-top: 20px;

      &-text {
        font-size: 18px;

        margin-bottom: 15px;
      }
    }
  }
}

@media (max-width: 520px) {
  .product-faq {

    &__item {
      padding: 10px 0;

      &-title {
        font-size: 18px;
        line-height: 25px;

        margin-right: 10px;
      }
    }

    &__content {

      &-text {
        font-size: 16px;
        line-height: 140%;
      }
    }
  }
}