.header {
  padding: 30px;

  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.15);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__menu {
    display: flex;

    &-link {
      width: 30px;
      height: 30px;
      margin-right: 37px;

      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__cart {
    position: relative;

    background-image: url('../img/Cart.svg');

    &-counter {
      position: absolute;
      left: 12px;
      top: 16px;

      color: var(--color-white);

      display: flex;
      align-items: center;
      justify-content: center;
      width: 25px;
      height: 25px;

      background-color: var(--color-teak);
      border-radius: 100%;
    }
  }

  &__search {
    background-image: url('../img/Search.svg');
  }
}

.nav {

  &--right {
    margin-right: 30px;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 40px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    font-size: 20px;
    color: var(--color-monsoon);

    &--slate {
      color: var(--color-slate);
    }

    &:hover {
      color: #00264b;
    }
  }
}

.mobile {
  display: none;
}

.burger {
  position: relative;
  width: 27px;
  height: 27px;

  &__line {
    display: block;
    width: 100%;
    height: 4px;

    background-color: var(--color-black);
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;

    display: block;
    width: 100%;
    height: 4px;

    background-color: var(--color-black);
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;

    display: block;
    width: 100%;
    height: 4px;

    background-color: var(--color-black);
  }
}

// media

@media(max-width: 1620px) {
  .nav {
    display: none;
  }

  .mobile {
    display: block;
  }
}

@media(max-width: 768px) {
  .header {
    padding-left: 0;
    padding-right: 0;

    &__logo {
      max-width: 217px;
      margin: 0 20px;
    }

    &__menu {

      &-link {
        margin-right: 0;
      }
    }
   
    &__search {
      display: none;
    }
  }
}