.card {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 555px;
  gap: 26px;

  &__item {
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--big {
      grid-column: 2 span;
    }

    &--1 {
      background-image: url('../img/card-1.png');
    }

    &--2 {
      background-image: url('../img/card-2.png');
    }

    &--3 {
      background-image: url('../img/card-3.png');
    }

    &--4 {
      background-image: url('../img/card-4.png');
    }

    &--5 {
      background-image: url('../img/card-5.png');
    } 
  }

  &__title {
    font-family: 'Outfit', sans-serif;
    font-weight: 300;
    font-size: 25px;
    letter-spacing: 0.265em;
    text-transform: uppercase;
    color: var(--color-white);
    
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 32px 0;

    background-color: #636a6a;
  }
}

@media(max-width: 1200px) {
  .card {
    grid-auto-rows: 400px;
  }
}

@media(max-width: 976px) {
  .card {
    grid-auto-rows: 380px;

    &__title {
      font-size: 20px;
    
      padding: 20px 0;
    }
  }
}

@media(max-width: 768px) {
  .card {
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: 450px;

    &__item {

      &--big {
        grid-column: 1 span;
      }
    }
  }
}

@media(max-width: 520px) {
  .card {
    grid-auto-rows: 380px;
  }
}