.sale {
  padding: 22px 0;

  background-color: var(--color-thumb);

  &__text {
    font-size: 20px;
    line-height: 156%;
    letter-spacing: 2px;
    color: var(--color-white);
    text-transform: uppercase;
    text-align: center;
  }
}

// media

@media(max-width: 768px) {
  .sale {

    &__text {
      font-size: 16px;
    }
  }
}