.time-count {
  margin-bottom: 40px;

  &__content {
    display: flex;
    align-items: center;
  }

  &__item {
    text-align: center;
  }

  &__val {
    min-width: 80px;
    font-family: 'Outfit', sans-serif;
    font-weight: 500;
    font-size: 65px;
    color: var(--color-white);
  }

  &__separator {
    font-family: 'Outfit', sans-serif;
    font-weight: 500;
    font-size: 65px;
    color: var(--color-white);

    margin-bottom: 14px;
    margin-right: 15px;
    margin-left: 15px;
  }
}

@media(max-width: 768px) {
  .time-count {
    margin-bottom: 12px;
    
    &__val {
      font-size: 35px;

      min-width: 45px;
    }

    &__separator {
      font-size: 35px;

      margin-right: 10px;
      margin-left: 10px;
    }
  }
}

@media (max-width: 520px) {
  .time-count {

    &__val {
      font-size: 30px;

      min-width: 40px;
    }

    &__separator {
      font-size: 30px;

      margin-right: 5px;
      margin-left: 5px;
      margin-bottom: 5px;
    }
  }
}